  width: 100%;
  color: #6b6b6b;
  letter-spacing: 1px;
  font-size: 20px;
  font-weight: 300;
}
.partners .btn__more {
  display: inline-block;
  padding: 17px 25px;
  border: 2px solid #ef4b4b;
  font-size: 22px;
  color: #ef4b4b;
  margin: 0 0 0 -4px;
  border-radius: 6px;
  transition: background .2s, color .2s;
}
.partners .btn__more:hover {
  background: #ef4b4b;
  color: #fff;
}
.partners .slick-slide {
  margin: 0 0 0 4px;
}
/* **************** PAGE-FOOTER ****************** */
.page-footer {
  background: #202020;
  padding: 40px 0 0 0;
}
.page-footer__wrap {
  display: flex;
  justify-content: space-between;
  padding: 0 0 37px 0;
}
.page-footer .logo {
  display: inline-block;
}
.page-footer .contacts {
  display: flex;
  padding: 20px 0 0 0;
}
.page-footer .contacts__item {
  display: flex;
  flex-direction: column;
  margin-right: 102px;
  font-weight: 300;
  font-size: 16px;
  letter-spacing: 0.8px;
}
.page-footer .contacts__item:last-child {
  margin-right: 0;
}
.page-footer .contacts__item a {
  color: #fff;
  margin-bottom: 18px;
  transition: color .2s;
}
.page-footer .contacts__item a:hover {
  color: #ef4b4b;
}
.page-footer .copy {
  background: #000;
  text-align: center;
  color: #616161;
  font-weight: 300;
  padding: 18px 0 15px 0;
  font-size: 17px;
  letter-spacing: 0.2px;
}
